import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Planes from '../views/Planes.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/planes',
    name: 'Planes',
    component: Planes
  }
]

const router = new VueRouter({
  routes
})

export default router
